<template>
    <div class="setComponent">
        <div class="form-item">
            <label>标题</label>
            <el-button size="mini" @click="butClick(['title', obj.title])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>描述</label>
            <el-button size="mini" @click="butClick(['smTitle', obj.smTitle])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>图片</label>
            <el-button size="small" type="primary" @click="uploadClick">上传图片</el-button>
        </div>
        <div class="form-item">
            <label>背景颜色</label>
            <el-select v-model="obj.background" placeholder="请选择">
            <el-option
                v-for="(item, index) in bgColorOptions"
                :key="index"
                :label="item"
                :value="item">
                <span class="bgColor" :style="{background: item}"></span>
            </el-option>
            </el-select>
        </div>
        <div class="form-item">
            <label>图片动画</label>
            <el-select v-model="obj.animateImg" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
        <div class="form-item">
            <label>主标题文字动画</label>
            <el-select v-model="obj.animateTitle" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
        <div class="form-item">
            <label>副标题文字动画</label>
            <el-select v-model="obj.animateText" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
import { bgColorOptions } from '@/utils/selectOptions'
export default {
    name: 'TextLeftImg',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            bgColorOptions: bgColorOptions
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('imgloadClick',this.obj)
        }
    }
}
</script>
<style lang="less" scoped>
    .bgColor{
        display: block;
        width: 30px;
        height: 30px;
    }
</style>

