<template>
    <div class="app-right setComponent">   
        <div class="tabs">
            <div class="tab1" :class="{active: activeName=='tab1'}" @click="tabChange('tab1')">
                <span slot="label"><i class="iconfont icon-editor"></i></span>
            </div>
            <div class="tab2" :class="{active: activeName=='tab2'}" @click="tabChange('tab2')">
                <span slot="label"><i class="iconfont icon-yangshi"></i></span>
            </div>
            <div class="tab3" :class="{active: activeName=='tab3'}" @click="tabChange('tab3')">
                <span slot="label"><i class="iconfont icon-quanbu-copy"></i></span>
            </div>
        </div>
        <div class="tabbox" v-if="activeName=='tab1'">
            <label class="title">内容设置</label>
            <component 
                @imgloadClick="imgloadClick" 
                @uploadClick="uploadClick" 
                @butClick="butClick" 
                @titleClick="titleClick"
                @bgClick="bgClick"
                :is="obj.type" 
                :obj="obj">
            </component>
        </div>
        <div class="tabbox" v-if="activeName=='tab2'">
            <label class="title">样式设置</label>
            <div class="form-item">
                <label>容器高度自适应</label>
                <el-switch
                    v-model="obj.heightAuto"
                >
                </el-switch>
            </div>
            
            <div class="form-item" v-if="!obj.heightAuto">
                <label>容器高度</label>
                <el-slider v-model="obj.height" :min="100" :max="1000"></el-slider>
            </div>
            <div class="form-item">
                <label>容器背景色</label>
                <el-color-picker v-model="obj.background"></el-color-picker>
            </div>
            <div class="form-item">
                <label>图片高度</label>
                <el-slider v-model="obj.imgHeight" :min="240" :max="600"></el-slider>
            </div>
            <div class="form-item">
                <label>容器内边距</label>
                <div class="margin-box">
                <input type="text" class="left" v-model="obj.paddingLeft">
                <input type="text" class="right" v-model="obj.paddingRight">
                <input type="text" class="top" v-model="obj.paddingTop">
                <input type="text" class="bottom" v-model="obj.paddingBottom">
                </div>
            </div>
            <div class="form-item">
                <label>容器外边距</label>
            <div class="margin-box">
                <input type="text" class="left" v-model="obj.marginLeft">
                <input type="text" class="right" v-model="obj.marginRight">
                <input type="text" class="top" v-model="obj.marginTop">
                <input type="text" class="bottom" v-model="obj.marginBottom">
            </div>
            </div>
        </div>
        <div class="tabbox" v-if="activeName=='tab3'">
            <label class="title">全局设置</label>
            <el-collapse v-model="globalActiveName" accordion>
                <el-collapse-item title="表单设置" name="1">
                    <div class="form-item">
                        <label>对齐方式</label>
                        <el-select @change="labelPostionChange" v-model="globalSetting.labelPosition" placeholder="请选择">
                            <el-option
                            v-for="(item, index) in Labeloptions"
                            :key="index"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="form-item">
                        <label>label宽度</label>
                        <el-slider @change="labelWidthChange" v-model="globalSetting.labelWidth" :min="30" :max="100"></el-slider>
                    </div>
                </el-collapse-item>
                <el-collapse-item title="颜色设置" name="2">
                    <div class="form-item">
                        <label>页面背景色</label>
                        <el-color-picker @change="backgroundChange" v-model="globalSetting.background"></el-color-picker>
                    </div>
                </el-collapse-item>
            </el-collapse>          
        </div>
    </div>
</template>

<script>
    import Html5Editor from '@/components/html5-editor'
    import SetComponent from '@/components/setComponent'
    import { mapGetters } from 'vuex'
    export default {
        props: {
            obj: {
                type: Object
            }
        },
        components: {
            ...SetComponent,
            Html5Editor
        },
        computed: {
            ...mapGetters([
                'globalSet',
            ])
        },
        data() {
            return {
                content: '',
                dialogVisible: false,
                type: {},
                activeName: 'tab1',
                globalActiveName: '',
                globalSetting: {
                    labelPosition: this.$store.state.globalSet.labelPosition,
                    labelWidth: this.$store.state.globalSet.labelWidth,
                    background: this.$store.state.globalSet.background
                },
                
                Labeloptions: [
                    {
                        label: '左对齐',
                        value: 'left'
                    },
                    {
                        label: '右对齐',
                        value: 'right'
                    },
                    {
                        label: '顶对齐',
                        value: 'top'
                    }
                ]
               
            }
        },
        methods: {
            // 更新编辑器内容
            updateData(content) {
                this.obj[this.type] = content
            },
            // 点击编辑 打开富文本编辑器编辑内容
            butClick(value) {
                this.$emit('butClick',value)
                this.dialogVisible = true
                this.content = value[1]
                this.type = value[0]             
            },
            // 右侧切换选项
            tabChange(tab) {
                this.activeName = tab
            },
            // 设置表单对齐方式
            labelPostionChange(value) {
                this.$store.dispatch('set_global_set_labelPosition',value)
            },
             // 设置表单label宽度
            labelWidthChange(value) {
                this.$store.dispatch('set_global_set_labelWidth',value)
            },
            // 背景颜色设置
            backgroundChange(value) {
                this.$store.dispatch('set_global_set_background',value)
            },
            // 保存 全局设置
            saveClick() {
                this.$store.dispatch('set_global_set', this.globalSetting)
            },
            // 多图片 切换 上传操作 如 轮播图 组件
            uploadClick(value) {
                this.$emit('uploadClick',value)
            },
            // 单图片 切换 上传操作 如image-text组件
            imgloadClick(value) {
                this.$emit('imgloadClick',value)
            },
            // 多卡片 编辑标题操作
            titleClick(value) {
                this.$emit('titleClick',value)
            },
            // 多卡片背景颜色设置
            bgClick(value) {
                this.$emit('bgClick',value)
            }
             
        }
    }
</script>

<style lang="less" scoped>
    .app-right{
        position: fixed;
        width: 200px;
        right: 0px;
        bottom: 0;
        top: 0px;
        background: #262626;
        z-index: 10;
        color:#fff;
        overflow-y: auto;
        overflow-x: hidden;
        z-index: 101;

        .tabs{
            display: flex;
            border-bottom: 1px solid #000;
            height: 60px;
            .iconfont{
                font-size: 18px;
            }
            .tab1,.tab2,.tab3{
                height: 60px;
                line-height: 60px;
                text-align: center;
                width: 60px;
                background-color:#262626;
                border: 1px solid #000;
            }
            .tab2,.tab3{
                border-left: none;
            }
            .active{
                border-bottom: none;
            }
        }
        .tabbox{
            padding: 10px;
            .title{
                display: block;
                padding: 0 10px 10px 0px;
                border-bottom: 1px solid #000;
                color:#f2f2f2;
                font-size: 16px;
            }
        }
        .el-collapse{
            border:none;
        }
        .el-collapse-item__header{
            background: #262626;
            color:#fff;
            border:none;
            height: 36px;
            line-height: 36px;
            border-bottom: 1px solid #000;
            font-weight: bold;
            font-size: 14px;
        }
        .el-collapse-item__content{
            background: #262626;
        }
        .el-collapse-item__wrap{
            border-bottom: 1px solid #000;
        }
        .margin-box{
            position: relative;
            width: 100%;
            height: 90px;
            input{
                position: absolute;
                width: 50px !important;
                height:30px !important;
            }
            .left{
                left: 0;
                top: 50%;
                margin-top: -15px;
            }
            .right{
                right: 0;
                top: 50%;
                margin-top: -15px;
            }
            .top{
                left: 50%;
                top: 0px;
                margin-left: -25px;
            }
            .bottom{
                left: 50%;
                bottom: 10px;
                margin-left: -25px;
            }
        }
    }
    
</style>

