<template>
    <el-tabs
        :style="{
            width: obj.width+'%',
            padding: obj.padding+'px'
        }" 
       :tab-position="obj.tabPosition" 
        v-model="obj.curTab" type="border-card">
        <el-tab-pane 
        :style="{ height: obj.height+'px'}"
        v-for="(column, key, columnIndex) in obj.childList" :key="columnIndex" 
        :label="(obj.tabList[columnIndex]).label" 
        :name="(obj.tabList[columnIndex]).value">
            <el-form label-width="60px">                         
                <draggable class="child-drag-box" tag="li" id="baseTab" @change="changeList" :list="column" @add="addEnd" v-bind="{group:{ name:'base' }, dragClass: 'dragClass'}">                                   
                    <div v-for="(child, childIndex) in column" :key="childIndex" class="child-component">
                        <span class="row-remove label" @click="delComponent(key,childIndex)"><i class="iconfont icon-shanchu"></i> 删除</span>
                        <span class="row-edit label" @click="setComponent(key, childIndex)"><i class="iconfont icon-editor"></i> 编辑</span>
                        <base-button :obj="child" v-if="child.type=='BaseButton'"></base-button>
                        <base-img :obj="child" v-if="child.type=='BaseImg'"></base-img>
                        <base-title :obj="child" v-if="child.type=='BaseTitle'"></base-title>
                        <base-p :obj="child" v-if="child.type=='BaseP'"></base-p>
                        <base-slider :obj="child" v-if="child.type=='BaseSlider'"></base-slider>
                        <form-id :obj="child" v-if="child.type=='FormId'"></form-id>
                        <form-input :obj="child" v-if="child.type=='FormInput'"></form-input>
                        <form-textarea :obj="child" v-if="child.type=='FormTextarea'"></form-textarea>
                        <form-radio :obj="child" v-if="child.type=='FormRadio'"></form-radio>
                        <form-select :obj="child" v-if="child.type=='FormSelect'"></form-select>
                        <form-checkbox :obj="child" v-if="child.type=='FormCheckbox'"></form-checkbox>
                    </div>   
                </draggable>
            </el-form>  
        </el-tab-pane>
    </el-tabs>             
</template>

<script>
import draggable from 'vuedraggable'
import Template from '@/components/template'
import FormInput from '@/components/template/form/FormInput'
import FormSelect from '@/components/template/form/FormSelect'
import FormRadio from '@/components/template/form/FormRadio'
import FormCheckbox from '@/components/template/form/FormCheckbox'
import FormTextarea from '@/components/template/form/FormTextarea'
import BaseTitle from '@/components/template/baseCss/BaseTitle'
import BaseImg from '@/components/template/baseCss/BaseImg'
import BaseP from '@/components/template/baseCss/BaseP'
import BaseButton from '@/components/template/baseCss/BaseButton'
import BaseSlider from '@/components/template/baseCss/BaseSlider'
import FormId from '@/components/template/FormId'
import deepClone from '@/utils/deepClone'
export default {
    name: 'BaseTab',
    components: {
        draggable,
        FormInput,
        FormSelect,
        FormRadio,
        FormCheckbox,
        FormTextarea,
        BaseTitle,
        BaseImg,
        BaseP,
        BaseButton,
        BaseSlider,
        FormId
    },
    props: {
        obj: {
            type: Object
        }
    },

    data() {
        return {
         
        }
    },
    methods: {

        changeList() {

        },
        // 删除组件
        delComponent(key, childIndex) {
            this.obj.childList[key].splice(childIndex,1)
            this.dispatch()
        },
        // 组件设置
        setComponent(key, childIndex) {          
            this.$emit('tabset',this.obj.childList[key][childIndex])
        },
        // 保存数据到vuex 
        dispatch() {
            this.$store.dispatch('set_template_list')
        },
        addEnd(item) {
            if(item.oldIndex == 4 && item.to.id == 'baseTab') {
                alert('Tab组件内不能拖入Tab组件')
                return
            }
        }
    }
}
</script>
<style lang="less">
    .el-tabs--left.el-tabs--border-card .el-tabs__header.is-left, .el-tabs__nav-wrap.is-left.is-scrollable{
        height: 100%;
    }
</style>
