import Vue from "vue";
import Router from "vue-router";
import Layout from "./views/Layout.vue";
import Preview from "./views/preview.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Layout",
      component: Layout
    },
    {
      path: "/preview",
      name: "preview",
      component: Preview
    },
  ]
});
