<template>
    <el-button @click="goClick" :circle="obj.cType=='circle'" :plain="obj.cType=='plain'" :round="obj.cType=='round'" :size="obj.size" :type="obj.bType">
        {{obj.value}}
    </el-button>
</template>
<script>

export default {
    name: 'BaseButton',
    props: {
        obj: {
            type: Object
        }
    },
    methods: {
        goClick() {
            if(this.obj.url) {
                window.open(this.obj.url)
            }
            
        }
    }
}
</script>


