<template>
    <div class="awesome-swiper"> 
      <div-row v-if="hardReset" :obj="obj">
        <div class="swiperbox">
            <div class="swiper" :style="{height: obj.height+'px'}">
                <swiper :options="obj.swiperOptionTop" class="gallery-top" ref="swiperTop">
                    <swiper-slide v-for="(slide, index) in obj.swiperSlides" :key="index" :style="{backgroundImage: `url(${slide})`,backgroundSize: 'cover'}"></swiper-slide>
                    <div v-if="obj.ifArrow" class="swiper-button-next swiper-button-white" slot="button-next"></div>
                    <div v-if="obj.ifArrow" class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
                </swiper>
                <swiper :style="{display: obj.ifThumbs? 'block': 'none'}" v-show="obj.ifThumbs" :options="obj.swiperOptionThumbs" class="gallery-thumbs" ref="swiperThumbs">
                    <swiper-slide v-for="(slide, index) in obj.swiperSlides" :key="index" :style="{backgroundImage: `url(${slide})`,backgroundSize: 'cover'}"></swiper-slide>
                </swiper>
            </div>
        </div>
      </div-row>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
      name: 'Swiper',
      props: {
          obj: {
              type: Object
          }
      },
      components: {
          swiper,
          swiperSlide
      },
      data() {
          return {
            hardReset: true
          }
      },
      watch: {
        'obj': {
          handler(newValue, oldValue) {
            this.hardReset= false
            this.$nextTick(() => {
                this.hardReset= true
            });
          },
          deep: true,
        }
      },
      mounted() {
          this.$nextTick(() => {
              this.hardReset= true
              if(this.hardReset) {
                const swiperTop = this.$refs.swiperTop.swiper
                const swiperThumbs = this.$refs.swiperThumbs.swiper
                swiperTop.controller.control = swiperThumbs
                swiperThumbs.controller.control = swiperTop
              }
              
          })         
          
      },
      methods: {

      }
    }
</script>
<style lang="less" scoped>
  .awesome-swiper{
      .swiperbox{
          width: 100%;
      }
  }
  .swiper-container {
    background-color: #000;
  }
  .swiper-slide {
    background-size: cover;
    background-position: center;
  }
  .gallery-top {
    height: 100%;
    width: 100%;
  }
  .gallery-thumbs {
    height: 20%;
    box-sizing: border-box;
    padding: 10px 0;
  }
  .gallery-thumbs .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
  }
  .gallery-thumbs .swiper-slide-active {
    opacity: 1;
  }
  .swiper-slide {
    font-size: 18px;
    color:#fff;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 40px 60px;
    background-color: transparent!important;
    justify-content: space-around!important;
  }
  .parallax-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 130%;
    height: 100%;
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center;
    background-image: url(https://surmon-china.github.io/vue-awesome-swiper/static/images/surmon-1.jpg);
  }
  .swiper-slide .title {
    font-size: 41px;
    font-weight: 300;
  }
  .swiper-slide .subtitle {
    font-size: 21px;
  }
  .swiper-slide .text {
    font-size: 14px;
    max-width: 400px;
    line-height: 1.3;
  }
  .swiper4 .swiper-slide img {
    width: 80%;
    height: auto;
    margin: 0 10%;
  }
  .swiper7 .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
  }
</style>
