<template>
    <div-row :obj="obj" class="content-wrapper text-picture">
        <el-row :gutter="obj.gutter">
            <el-col :span="24/obj.columns" v-for="(item,index) in obj.imgTextBox" :key="index">
                <el-card class="mt10">
                    <div @click="goClick(item.httpUrl)" class="img image" :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                        <img :src="item.imgUrl" :style="{height:obj.imgHeight+'px'}">
                    </div>
                    <div class="text-box">
                        <h2 class="text-title" :class="obj.animateTitle? 'wow '+obj.animateTitle : ''" v-html="item.title"></h2>
                        <div class="bottom clearfix">
                        <p class="text-desc" :class="obj.animateText? 'wow '+obj.animateText : ''" v-html="item.desc"></p>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>     
    </div-row>
</template>
<script>
import animate from '@/mixins/animate'
export default {
    name: 'ThreeColumns',
    mixins: [animate]
    
}
</script>
<style lang="less" scoped>
    .content-wrapper{
        .image{
            overflow: hidden;
            position: relative;
            margin: 0 auto;
            img{
                height: 240px;
                object-fit: cover;
                margin: auto;
                width: 100%;
                display: block;
                transform:scale(1);
                transition: 1s all ease;
                &:hover{
                    transform:scale(1.2);  
                }
            }         
        }
        .text-box{
            .text-title,.text-desc{
                text-align: left;
                overflow: hidden;
                text-overflow:ellipsis;
            }
            .text-title{
                width: 80%;
                padding: 20px 15px;
                white-space: nowrap;
            }
            .text-desc{
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                line-height: 20px;
                min-height: 60px;
                font-size: 13px;
            }
        }
    }
</style>



