<template>
    <div class="app-left">
        <div class="app-left-nav">           
            <div class="nav nav-groups" :class="{active: listIndex==curIndex }" v-for="(list,listIndex) in layout" :key="listIndex">
                <div class="nav-header">{{list.title}}</div>
                <div class="nav-rows" v-if="list.type=='containerComponent'">              
                    <draggable class="clearfix" tag="li" v-model="list.list" v-bind="{group:{ name:'row',  pull:'clone', put:false}, dragClass: 'dragClass', scroll: true, sort:false}" @end="moved">
                        <div v-for="(element,elIndex) in list.list" :key="elIndex" class="el-row">
                            <div class="row-text">{{element.iconText}}</div>
                            <!-- <el-row class="row-fluid">
                                <span class="row-tag">Row</span>
                                    <el-col class="row-column" :span="24/element.type" v-for="(column,key) in element.childList" :key="key">
                                       <span class="row-tag">Column</span>
                                    </el-col>
                            </el-row> -->
                        </div>
                    </draggable>
                </div>
                <!-- 动态组件单独设置 -->
                <div class="nav-rows" v-else-if="list.type=='AdminComponent'">   
                    <el-form>   
                        <div class="setComponent adminSearch">  
                            <div class="form-item" style="padding: 4px 0 0 16px;cursor: pointer;">
                                <span class="ft14" style="color:skyBlue;">选择表单树</span>
                            </div> 
                            <div class="form-item">
                                <input type="text" v-model="formId">
                                <span class="search"><i class="el-icon-search"></i></span>
                            </div>                                
                        </div>      
                        <draggable class="clearfix" tag="li" v-model="list.list" v-bind="{group:{ name:'base',  pull:'clone', put:false}, dragClass: 'dragClass', scroll: true, sort:false}" @end="moved">
                            <div v-for="(element,elIndex) in list.list" :key="elIndex" class="el-row">
                                <div class="nav-component">
                                    <div class="nav-component-title">{{element.formDefName}} {{element.formDefCode}}</div>
                                </div>
                            </div>
                        </draggable>
                    </el-form> 
                </div>
                <div class="nav-rows" v-else>   
                    <el-form>                     
                        <draggable class="clearfix" tag="li" v-model="list.list" v-bind="{group:{ name:'base',  pull:'clone', put:false}, dragClass: 'dragClass', scroll: true, sort:false}" @end="moved">
                            <div v-for="(element,elIndex) in list.list" :key="elIndex" class="nav-component-box">
                                <div v-if="element.iconImg">
                                    <div class="nav-component-img">
                                        <img :src="element.iconImg" />
                                    </div>
                                    <div class="nav-component-title">{{element.iconText}}</div>
                                </div>
                                <div v-else class="nav-icon-box">
                                    <i class="iconfont" :class="element.icon"></i>
                                    <span>{{element.iconText}}</span>
                                </div>
                            </div>
                        </draggable>
                    </el-form> 
                </div>
            </div>           
        </div>
    </div>
</template>
<script>
import draggable from 'vuedraggable'
import ContainerComponent from '@/utils/template/containerList'
import BaseCssComponent from '@/utils/template/baseCssList'
import moduleComponent from '@/utils/template/moduleList'
import ComponentList from '@/components/template'

export default {
    components: {
        draggable,
        ...ComponentList
    },
    created() {
        // this.getData()
    },
    data() {
        return {
            curIndex: 0,
            formId: '',
            layout: [
                ContainerComponent,
                BaseCssComponent,
                moduleComponent            
            ]
        }
    },
    methods: {
        moved(value) {
            this.$emit('moved',value)
        },
        navClick(index) {
            this.curIndex = index
        },
        getData() {
           this.$http.get('/form/findFormList.action?condition=&sort=form_def_id&order=order&offset=0&limit=10&formDefFolderId=1&pageSize=1&swaggerUserCode=sysadmin')
            .then(res=>{
                this.layout[this.layout.length-1].list = res.data.rows
                this.layout[this.layout.length-1].list.map(item=>{
                    item.type = 'FormId'
                })  
            })
        }
    }
}
</script>

<style lang="less">
    .app-left{
        position: fixed;
        width: 220px;
        left: 0px;
        bottom: 0;
        top: 60px;
        background: #262626;
        padding: 9px 0;
        z-index: 10;
        -webkit-transition: all 500ms ease;
        -moz-transition: all 500ms ease;
        -ms-transition: all 500ms ease;
        -o-transition: all 500ms ease;
        transition: all 500ms ease;
        overflow: auto;
        
        .nav-groups{
            color:#fff;
            padding: 10px 20px;    
            .nav-header{
                font-size: 16px;
                text-align: center;
                
            }
            .nav-rows{                          
                .el-row{
                    background:#ccc;
                    width: 48%;
                    color:#666;
                    border-radius: 2px;
                    padding:6px;
                    margin-top:10px;
                    margin-left: 2%;
                    float: left;
                    height: 30px;
                    position: relative;
                    overflow: hidden;
                    .row-text{
                        font-size: 12px;
                        cursor: move;
                    }
                    .row-fluid{
                       opacity: 0;
                    }
                }
               
                .dragClass{
                    background: skyblue;
                }
            }
            .nav-component{     
                .nav-component-title{
                    font-size: 12px;
                    cursor: move;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    white-space:nowrap

                }
                // .nav-componnet-box{
                //     opacity: 0;
                //     height: 0;
                // }
               
            }
             .nav-icon-box{
                font-size: 12px;
                display: block;
                width: 100%;
                line-height: 26px;
                position: relative;
                float: left;
                left: 0;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                cursor: move;
                background: #f4f6fc;
                color:#333;
                padding: 2px 8px;
                .iconfont{
                    margin-right: 5px;
                }
            }
            
        }
        // .nav-groups.active{
        //     max-height: 1000px;
        //     transition: all 1000ms ease-in;
        // }
        .adminSearch{
            .form-item{
                position:relative;
                input{
                    padding-right: 25px;
                }
            }
            .search{
                position:absolute;
                right: 0;
                top:10px;
                width: 20px;
                height: 30px;
                line-height: 30px;
                cursor: pointer;
            }
        }
        .nav-component-box{
            width: 48%;
            color:#c9c9c9;
            border-radius: 2px;
            // padding:6px;
            margin-top:10px;
            margin-left: 2%;
            float: left;
            position: relative;
            // overflow: hidden;
            cursor: move;
            .nav-component-title{
                font-size: 12px;              
            }
            .nav-component-img{
                width: 100%;
                img{
                    width: 100%;
                    border: none;
                }
            }
        }
        
    }
    
</style>

